    background-position: center 10vh; }
  to {
    margin-top: 0;
    background-position: center 100%; } }

.headerBgIn {
  animation-name: headerBgIn; }

@keyframes cardsShadowAppear {
  from {
    box-shadow: 0 0 0 0 transparent; }
  to {
    box-shadow: 0 20px 60px -10px rgba(0, 0, 0, 0.4); } }

.cardsShadowAppear {
